import { mapState, mapGetters } from 'vuex'
import Breadbar from "~/components/Breadbar.vue";
export default {
  name: "homepage",
  components: [ Breadbar ],
  computed: {
    ...mapState(["accessToken"]),
  },
  data() {
    return {
      status: 1,
      post: [],
      genders: [
        { text: 'Man' },
        { text: 'Woman' },
        { text: 'Gay' },
        { text: 'Resbian' },
        { text: 'ETC' },
      ],
      pageslug: this.$route.params.slug,
    }
  },
  mounted () {
    this.getPostSlug()
  },
  methods: {
    getPostSlug() {
      this.post = []
      this.loading = true
      this.$axios.$get('/v1/posts.one?id='+this.$route.params.slug, {
        headers: {"x-rotor-auth": this.accessToken}
      }).then((response) => {
        console.log('test')
        console.log(response)
        this.post = response.data
      }).catch((error) => {
        console.log(error)
        this.loading = false
      })
    },
    viewRoute(link) {
      this.$router.push({
        path: link
      });
    },
    redirectTo(path) {
      this.$router.replace({ path });
    }
  }
};
